<template>
  <div class="credit-account-management">
    <el-new-card v-if="$route.matched.length <= 2">
      <template slot="header">
        <span>{{ $route.meta.name }}</span>
        <el-button v-if="canCreateAcct" plain icon="el-icon-plus" @click="toCreateAccount">创建积分账户</el-button>
      </template>

      <el-new-table :apiName="apiName">
        <el-table-column prop="account" label="积分账户名称" align="center" min-width="120">
          <template slot-scope="scope">
            <el-link type="primary" @click="toAccountDetail(scope.row)">{{ scope.row.account }}</el-link>
          </template>
        </el-table-column>

        <el-table-column prop="deptName" label="部门 / 企业名称" align="center" min-width="120"></el-table-column>

        <el-table-column prop="status" label="状态" align="center">
          <template slot-scope="scope">
            <acct-sts-icon :status="scope.row.status"></acct-sts-icon>
          </template>
        </el-table-column>

        <el-table-column prop="budget" label="预算（元）" align="center" min-width="120">
          <template slot-scope="scope">
            <span>{{ scope.row.budget | fmtMoney }}</span>
          </template>
        </el-table-column>

        <el-table-column prop="budgetAmount" label="预算额度" align="center" min-width="120">
          <template slot-scope="scope">
            <span>{{ scope.row.budgetAmount | fmtMoney }}</span>
          </template>
        </el-table-column>

        <el-table-column prop="budgetAmountLine" label="预算额度警戒线" align="center" min-width="120">
          <template slot-scope="scope">
            <span>{{ scope.row.budgetAmountLine | fmtFloat2Percent }}</span>
          </template>
        </el-table-column>

        <el-table-column prop="imprest" label="备用金（元）" align="center" min-width="120">
          <template slot-scope="scope">
            <span>{{ scope.row.imprest | fmtMoney }}</span>
          </template>
        </el-table-column>

        <el-table-column prop="imprestAmount" label="备用额度" align="center" min-width="120">
          <template slot-scope="scope">
            <span>{{ scope.row.imprestAmount | fmtMoney }}</span>
          </template>
        </el-table-column>

        <el-table-column prop="imprestAmountLine" label="备用额度警戒线" align="center" min-width="120">
          <template slot-scope="scope">
            <span>{{ scope.row.imprestAmountLine | fmtFloat2Percent }}</span>
          </template>
        </el-table-column>

        <el-table-column prop="usedAmount" label="已用额度" align="center" min-width="120">
          <template slot-scope="scope">
            <span>{{ scope.row.usedAmount | fmtMoney }}</span>
          </template>
        </el-table-column>

        <el-table-column prop="availableAmount" label="可用额度" align="center" min-width="120">
          <template slot-scope="scope">
            <span>{{ scope.row.availableAmount | fmtMoney }}</span>
          </template>
        </el-table-column>
      </el-new-table>
    </el-new-card>

    <router-view v-else />
  </div>
</template>

<script>
import ElNewCard from '@/components/ElNewCard'
import ElNewTable from '@/components/ElNewTable'
import AcctStsIcon from '@/components/status/AcctStsIcon'
import { mapGetters } from 'vuex'

export default {
  name: 'credit-account-management',
  components: {
    ElNewCard,
    ElNewTable,
    AcctStsIcon
  },
  data () {
    return {
      apiName: 'serviceGetAcctList'
    }
  },
  computed: {
    ...mapGetters({
      accountInfo: 'getAccountInfo'
    }),

    canCreateAcct () {
      return this.accountInfo.isAdmin || this.accountInfo.type === 'GTMC'
    }
  },
  methods: {
    toCreateAccount () {
      this.$router.push('/crdtAcctMgmt/createAcctAppl')
    },

    toAccountDetail (row) {
      let url = ''

      if (row.status === 0 || row.status === 1) {
        url = '/crdtAcctMgmt/acctDetail'
      } else if (row.status === 2 || row.status === 3 || row.status === 4 || row.status === 5) {
        url = '/crdtAcctMgmt/acctApplDetail'
      }

      url && this.$router.push({ path: url, query: { id: row.id } })
    }
  }
}
</script>

